            <h4 class="modal-title">Agregar un servicio</h4>
          </div>
          <div class="modal-body">
            <form action="">
              <div class="form-group">
                <label for="">Cliente</label>
                <select name="" v-model="nueva_asignacion.socio" class="form-control" id="">
                  <option v-for="socio in socios" :value="socio.idsocio">
                    {{socio.nombre}}
                  </option>
                </select>
              </div>
              <div class="form-group">
                <label for="">Personal</label>
                <input type="text"
                       v-model="nueva_asignacion.personal"
                       class="form-control">
              </div>
              <div class="form-group">
                <label for="">Placa</label>
                <input type="text"
                       v-model="nueva_asignacion.placa"
                       class="form-control">
              </div>
              <div class="form-group">
                <label for="">Modelo</label>
                <input type="text"
                       v-model="nueva_asignacion.modelo" 
