              </div>
              <div class="form-group">
                <label for="">Ao</label>
                <input type="text" 
                       v-model="nueva_asignacion.anio"
                       class="form-control">
              </div>
              <div class="form-group">
                <label for="">Tipo</label>
                <select name=""
                        v-model="nueva_asignacion.tipo"
                        class="form-control"
                        id="">
                  <option v-for="tipo in tipos" :value="tipo.idtiposervicio">{{tipo.nombre}}</option>
                </select>
              </div>
            </form>




          </div>
          <div class="modal-footer">            
            <button type="button" 
                    @click="insertar_asignacion" 
                    class="btn btn-primary">Registrar</button>
          </div>
        </div><!-- /.modal-content -->
      </div><!-- /.modal-dialog -->
    </div>




