    }
    ,methods:{
      formato_fecha:function(fecha){
        //1.-Obtengo el mes y sumo 1
        var mes=fecha.getMonth()+1;

        //Para poder concatenarle '0' convierto el mes a cadena
        var label_mes=mes.toString();

        //Si el mes es menor que 10
        if(mes<10)
          //A la cadena del mes le concateno '0'
          label_mes='0'+label_mes;

        //Realizo un procedimiento similar con la fecha
        var dia=fecha.getDate();
        var label_dia=dia.toString();

        if(dia<10)
          label_dia='0'+label_dia;


        //Al final devuelvo la concatenacion del anio, del mes con el formato adecuado y el dia
        return fecha.getFullYear()+'-'+label_mes+'-'+label_dia;
      }
      ,ver_dia:function(fecha){
        this.nueva_asignacion.fecha=this.formato_fecha(fecha);
        
        if(this.sincrono){
          //Comunicacion sincrona
          document.querySelector('#oculto').value=this.formato_fecha(fecha);
          document.frm.submit();
        }
        else{
          //Comunicacion asincrona
          //Peticion get con axios
        //Preparar la informacion que voy a enviar
        var datos = {
          fecha:this.formato_fecha(fecha)
          ,_token:this.token
        };

        //Cuando desde axios haces una peticion y requieres enviar datos, lo mejor es enviarlo por POST

        axios.post(this.url,datos).then((result) => {
          for(var i=0;i<result.data.length;i++){
            this.asignaciones.push(result.data[i]);
          }
        });
        //Peticion get con axios
        }
      }

      ,mostrar_modal:function(fila,columna){
        this.nueva_asignacion.fila=fila;
        this.nueva_asignacion.columna=columna;
        //this.nueva_asignacion.cliente='';
        //this.nueva_asignacion.tipo='Normal';
        $('#modal').modal('show');
      }

      ,insertar_asignacion:function(){
        

        var datos = {
          servicio:this.nueva_asignacion
          ,_token:this.token
        };



        /*let url_insertar;
        let url_insertar2;

        const insertar1 = axios.post(url_insertar);
        const insertar2 = axios.post(url_insertar2);
        axios.all([insertar1, insertar2],datos)
        .then((result) => {
          if(result.data.status=='Error'){
                //1.-El programa funciona pero el proceso no se pudo realizar
                alert(result.data.mensaje);
              }
              else{
                //2.- El programa funciona y el proceso se pudo realizar
                alert('El servicio se guard correctamente');
                this.asignaciones.push({
                  fila:this.nueva_asignacion.fila
                 ,columna:this.nueva_asignacion.columna
                 ,cliente:result.data.cliente
                 ,tipo:result.data.tipo
                });
                $('#modal').modal('hide');
              }
        })
        .catch(function(error){
              //3.-El servidor tuvo un error
              alert('Hubo un error en el servidor');
              console.log(error)
        });*/



          /*axios.all([
            axios.post(this.url_insertar, datos),
            axios.post(this.url_insertar2, datos)
            ])        
             .then((result) => {
              if(result.data.status=='Error'){
                //1.-El programa funciona pero el proceso no se pudo realizar
                alert(result.data.mensaje);
              }
              else{
                //2.- El programa funciona y el proceso se pudo realizar
                alert('El servicio se guard correctamente');
                this.asignaciones.push({
                  fila:this.nueva_asignacion.fila
                 ,columna:this.nueva_asignacion.columna
                 ,cliente:result.data.cliente
                 ,tipo:result.data.tipo
                });
                $('#modal').modal('hide');
              }             
            })
           .catch(function(error){
              //3.-El servidor tuvo un error
              alert('Hubo un error en el servidor');
              console.log(error)
           });*/



        //Cuando desde axios haces una peticion y requieres enviar datos, lo mejor es enviarlo por POST

        axios.post(this.url_insertar,datos)             
             .then((result) => {
              if(result.data.status=='Error'){
                //1.-El programa funciona pero el proceso no se pudo realizar
                alert(result.data.mensaje);
              }
              else{
                //2.- El programa funciona y el proceso se pudo realizar
                alert('El servicio se guard correctamente');
                this.asignaciones.push({
                  fila:this.nueva_asignacion.fila
                 ,columna:this.nueva_asignacion.columna
                 ,cliente:result.data.cliente
                 ,tipo:result.data.tipo
                });
                $('#modal').modal('hide');
              }             
            })
           .catch(function(error){
              //3.-El servidor tuvo un error
              alert('Hubo un error en el servidor');
              console.log(error)
           });


        axios.post(this.url_insertarpuntos,datos)             
             .then((result) => {
              if(result.data.status=='Error'){
                //1.-El programa funciona pero el proceso no se pudo realizar
                //alert(result.data.mensaje);
              }
              else{
                //2.- El programa funciona y el proceso se pudo realizar
                /*alert('El servicio se guard correctamente');
                this.asignaciones.push({
                  fila:this.nueva_asignacion.fila
                 ,columna:this.nueva_asignacion.columna
                 ,cliente:result.data.cliente
                 ,tipo:result.data.tipo
                });*/
                $('#modal').modal('hide');
              }             
            })
           .catch(function(error){
              //3.-El servidor tuvo un error
              //alert('Hubo un error en el servidor');
              console.log(error)
           });



       console.log(this.nueva_asignacion);
         
      }





    }
    ,components:{
        'tablatiempo':TimeTable
       ,'datepicker' :Datepicker
    }
  }
  
</script>

