<template>
  <td :class="obtener_formato_celda(registro)"
      @click="activar_celda">
      <div v-if="registro">
        <span class="padding-5px-tb padding-15px-lr border-radius-5 margin-10px-bottom text-white font-size16 xs-font-size13" 
              :class="formato_tipo(registro.tipo)"
              >
              {{registro.tipo}}</span>
        <div class="margin-10px-top font-size14">
          {{registro.cliente}}
        </div>
        <input type="text" class="form-control" @input="avisar_papa($event)">
      </div> 
  </td>
</template>



<script>
  export default{
    data (){
      return {
        dato1:'Este es un dato interno'
      }
    }
    ,props:['registro',"fila","columna"]
    ,components:{
        
    }
    ,methods:{
      formato_tipo:function (tipo){
         switch(tipo){
           case 'Normal':
           return{
           'bg-sky':true
           }
           break;
           case 'Vip':
           return{
           'bg-green':true
           }
           break;
           case 'Diamante':
           return{
           'bg-purple':true
           }
           break;
         }
        }
      ,obtener_formato_celda:function(asignacion){        
        if(!asignacion)
          return{'bg-light-gray':true};
      }
      ,activar_celda:function(){
        if(!this.registro)
          this.$emit("click-celda",this.fila,this.columna);
      }
      ,avisar_papa:function(event){
        this.$emit("captura-observacion",event.target.value);
        
      }
    }
  }
  
</script>



<style>

.bg-sky.box-shadow {
    box-shadow: 0px 5px 0px 0px #00a2a7
}

.bg-orange.box-shadow {
    box-shadow: 0px 5px 0px 0px #af4305
}

.bg-green.box-shadow {
    box-shadow: 0px 5px 0px 0px #4ca520
}

.bg-yellow.box-shadow {
    box-shadow: 0px 5px 0px 0px #dcbf02
}

.bg-pink.box-shadow {
    box-shadow: 0px 5px 0px 0px #e82d8b
}

.bg-purple.box-shadow {
    box-shadow: 0px 5px 0px 0px #8343e8
}

.bg-lightred.box-shadow {
    box-shadow: 0px 5px 0px 0px #d84213
}


.bg-sky {
    background-color: #02c2c7
}

.bg-orange {
    background-color: #e95601
}

.bg-green {
    background-color: #5bbd2a
}

.bg-yellow {
    background-color: #f0d001
}

.bg-pink {
    background-color: #ff48a4
}

.bg-purple {
    background-color: #9d60ff
}

.bg-lightred {
    background-color: #ff5722
}

</style>



