<template>
	<div>
        <div v-if="observacion!=''" class="alert alert-warning" role="alert">
            {{observacion}}
        </div>
	    <div class="container">
	        <div class="timetable-img text-center">
	            
	        </div>
	        <div class="table-responsive">
	            <table class="table table-bordered text-center">
	                <thead>
	                    <tr class="bg-light-gray">
	                        <th class="text-uppercase">{{etiqueta_columnas}}
	                        </th>
	                        <th class="text-uppercase"
	                            v-for="columna in columnas"
	                        >{{columna.label}}</th>
	                        
	                    </tr>
	                </thead>
	                <tbody>
	                    <tr v-for="fila in filas">
	                        <td class="align-middle">{{fila.label}}</td>
	                        <evento v-for="columna in columnas" 
                                    v-bind:key="columna.iden+'_'+fila.iden"
                                    :registro="obten_asignacion(fila.iden,columna.iden)"
                                    :fila="fila.iden"
                                    :columna="columna.iden"
                                    @captura-observacion="mostrar_observacion"
                                    @click-celda="registrar_asignacion"
                                    ></evento>

	                        
	                    </tr>                    
	                </tbody>
	            </table>
	        </div>
	    </div>

	    
  </div>

</template>



<script>
    import Event from './Event.vue';
	export default{
    data (){
      return {
        msg: 'Welcome to Your Vue.js App'
        ,etiqueta_columnas: 'Estaciones'
        ,mensaje_hijo:{nombre:'Pedro',tipo:'Normal'}
        ,observacion:''
               
      }
    }
    ,props:['filas','columnas','asignaciones']
    ,components:{
        'evento':Event
    }
    ,methods:{
      obten_asignacion: function(fila,columna){
        var resultado=this.asignaciones.filter(function(item){
          if((item.fila==fila)&&(item.columna==columna))
            return true;
          else
            return false;
        });
        if(resultado.length==0)
          return null;
        else
          return resultado[0];
      }
     ,obten_asignacion2: function(fila,columna){
        //la bandera apagada es -1
        var bandera=-1;
        for(var i=0;i<this.asignaciones.length;i++){
          if((this.asignaciones[i].fila==fila)&&(this.asignaciones[i].columna==columna))
            bandera=i;
        }

        if(bandera=-1)
          return null;
        else
          return this.asignaciones[bandera];
      }
     ,registrar_asignacion:function(fila,columna){
     //alert('Hola desde la fila '+fila+'y la columna '+columna);
     
     console.log(this.nueva_asignacion);
     
     this.$emit('celda_activa',fila,columna);
     }
     
     ,mostrar_observacion:function(mensaje){
        this.observacion=mensaje;
     }
    }


  }
</script>



<style>
body{
    margin-top:20px;
}
.bg-light-gray {
    background-color: #f7f7f7;
}
.table-bordered thead td, .table-bordered thead th {
    border-bottom-width: 2px;
}
.table thead th {
    vertical-align: bottom;
    border-bottom: 2px solid #dee2e6;
}
.table-bordered td, .table-bordered th {
    border: 1px solid #dee2e6;
}




.padding-15px-lr {
    padding-left: 15px;
    padding-right: 15px;
}
.padding-5px-tb {
    padding-top: 5px;
    padding-bottom: 5px;
}
.margin-10px-bottom {
    margin-bottom: 10px;
}
.border-radius-5 {
    border-radius: 5px;
}

.margin-10px-top {
    margin-top: 10px;
}
.font-size14 {
    font-size: 14px;
}

.text-light-gray {
    color: #d6d5d5;
}
.font-size13 {
    font-size: 13px;
}

.table-bordered td, .table-bordered th {
    border: 1px solid #dee2e6;
}
.table td, .table th {
    padding: .75rem;
    vertical-align: top;
    border-top: 1px solid #dee2e6;
}
</style>


